import axios from "axios";
import { useState } from "react";
import { useNavigate } from "react-router-dom";

const Reset = () => {
    const navigate = useNavigate();
    const [email,setEmail] = useState("");
    const [name, setName] = useState("");
    const [id,setId] = useState();
    const [PW,setPW] = useState("");
    const [PW2,setPW2] = useState("");
    const emailHandler = (e) => {
        setEmail(e.target.value);
    }
    const PWHandler = (e) => {
        setPW(e.target.value);
    }
    const onPW2Blur = (event) => {
        setPW2(event.target.value);
        if(event.target.value!==PW){
          document.querySelector(".pw_check_guide").classList.remove("hidden");
        }
        else {
          document.querySelector(".pw_check_guide").classList.add("hidden");
        }
      }
    const nameHandler = (e) => {
        setName(e.target.value);
    }
    const PWCheck = () => {
        let RegExp1 = /(?=.*?[a-z])(?=.*?[A-Z])/;
        let RegExp2 = /(?=.*?[0-9])(?=.*?[#?!@$%^&*-])/;
        let RegExp3 = /^.{8,18}$/;
        if(RegExp1.test(PW)&&RegExp2.test(PW)&&RegExp3.test(PW))return true;
        else return false;
      }
    const onResetClicked = () => {
        if(PWCheck&&PW===PW2){
            const uri = "api/v1/auth/reset";
            const data = {id:id, password:PW};
            axios.put(uri,data)
            .then(()=>{
                navigate("/login");
            })
            .catch((err)=>{
                alert("   ");
                console.log(err);
            });
        }
    }
    const onCheckClicked = ()=>{
        const uri = "/api/v1/auth/check";
        const data = {name:name,email:email};
        axios.post(uri,data)
        .then((res)=>{
            setId(res.data);
            const temp = document.querySelector(".reset_input");
            temp.classList.remove("hidden");
        })
        .catch((err)=>{
            alert("    ");
            console.log(err);
        })
    }
    return (
        <div id="container">
            <div id="content">
                <form>
                    <div className="login">
                        <div className='reset'>
                            <h3 id='login-title'> </h3>
                            <div className="reset_notice">    </div>
                            <fieldset>
                                <ul className='form'>
                                    <li>
                                        <label><input id="member-id" type="text" onChange={emailHandler}placeholder=' '/></label>
                                    </li>
                                    <li>
                                        <label><input id="member-pw" type="text" onChange={nameHandler}placeholder=''/></label>
                                    </li>
                                </ul>
                                <ul className='btn_login'>
                                    <li>
                                        <div onClick={onCheckClicked}><a></a></div>
                                    </li>
                                </ul>
                                <ul className='reset_input form hidden'>
                                    <li>
                                        <label><input id="member-id"onChange={PWHandler} type="password" placeholder=' '/></label>
                                    </li>
                                    <li>
                                        <label><input id="member-pw" onBlur={onPW2Blur}type="password"placeholder=' '/></label>
                                        <span className="pw_check_guide hidden"tyle={{color:"blue",fontSize:"13px"}}>  </span>  
                                        <p className='text_guide'>
                                     8~16   , ,  .
                                    </p>
                                    </li>
                                    <ul className='btn_login'>
                                        <li>
                                            <div onClick={onResetClicked}><a></a></div>
                                        </li>
                                    </ul>
                                </ul>
                                
                                
                            </fieldset>
                        </div>
                    </div>
                </form>
            </div>
        </div>
    )
}

export default Reset;